import Base, { BaseType } from './Base';
import JourneyDesigner, { JourneyDesignerType } from './JourneyDesigner';

function getMode(): ThemeMode {
  return 'light';
}

type ThemeMode = 'light' | 'dark';
type FtosDesign = {
  getMode: () => ThemeMode;
};

type DesignSystem<T> = T & {
  Base: BaseType;
  JourneyDesigner: JourneyDesignerType;
};
export default { getMode, Base, JourneyDesigner } as DesignSystem<FtosDesign>;
export { BaseType, JourneyDesignerType };
